import React from 'react'

const Button = ({ colors, label, rounded, url }) => (
  <div>
    <a href={url} alt={label}>
      <button
        className="bg-highlight text-accent w-full rounded p-8 leading-none font-bold shadow-md hover:shadow-xl transform transition duration-500 hover:scale-101"
        style={
          colors && {
            background: colors.highlight,
            color: colors.accent,
            borderRadius: `${rounded}px`
          }
        }
      >
        {label}
      </button>
    </a>
  </div>
)

export default Button
